
.application-designer-message{
	padding: 1rem;
	font-size: 0.875rem;
}
.input-list li{
	padding: 1rem 0;
	border-bottom: 1px solid #e5e5e5;
}
.input-list input{
	font-size: 0.875rem;
}
.file-wrap{
	padding: 1rem 0;
	color: #666;
}
.tips{
	font-size: 0.75rem;
	line-height: 1.5rem;
	margin-bottom: 1rem;
}
.file-box{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	display: -webkit-flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-webkit-flex-wrap: wrap;
}
.file-box>div{
	-ms-flex-negative:0;
	    flex-shrink:0;
	-webkit-flex-shrink:0;
}
.camera{
	width: 4.6rem;
	height: 4.6rem;
	padding: 1.5rem;
	margin: 0.5rem 0.4rem;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	border:1px dashed #e5e5e5;
}
.file-img{
	width: 25%;
	padding: 0.5rem;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.designer-tcp{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	display: -webkit-flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-justify-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-align-items: center;
}
.dagou{
	width: 1.5rem;
	height: 1rem;
	background: url("../assets/ico_gou1.png") left no-repeat;
	background-size: auto 1rem;
}
.isSelect{
	background: url("../assets/ico_gou2.png") left no-repeat;
	background-size: auto 1rem;
}
.dagou img{
	width: auto;
	height: 1rem;
}
.submit{
	text-align: center;
	background-color: #000;
	color: #fff;
	line-height: 2.5rem;
	border-radius: 0.25rem;
	margin-top: 1rem;
}



