
.order-details{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	display: -webkit-flex;
	padding: 1rem;
	font-size: 0.875rem;
}
.order-icon{
	width: 3.5rem;
	height: 3.5rem;
	background: url("../assets/clothes-bg.png") center left no-repeat;
	background-size: 100% auto;
	-ms-flex-negative:0;
	    flex-shrink:0;
	-webkit-flex-shrink:0;
	margin-right: 1rem;
}
.cause{
	padding: 0.8rem;
}
.select-box{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	display: -webkit-flex;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	-webkit-justify-content: space-between;
	background: url("../assets/ico-right.png") right no-repeat;
	background-size: auto 0.8rem;
	padding-right: 0.8rem;
}
.order-style{
	color: #999;
}
.cause .text{
	color: #999;
	font-size: 0.875rem;
	margin-top: 0.5rem;
}
.describe{
	padding: 0.8rem;
	position: relative;
}
.describe .title{
	color: #666;
}
.describe textarea{
	width: 100%;
	height: 7rem;
	background-color: #F3F3F3;
	font-size: 0.75rem;
	resize:none;
	padding: 0.5rem 0.5rem 1.5rem 0.5rem;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	margin-top: 0.8rem;
}
.describe .input-num{
	color: #666;
	font-size: 0.75rem;
	position: absolute;
	right: 1.5rem;
	bottom: 1.5rem;
}
.file-imgs-box{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	display: -webkit-flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-webkit-flex-wrap: wrap;
	padding: 0.3rem;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.file-img{
	width: 25%;
	padding: 0.5rem;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.camera{
	width: 2rem;
	padding: 0 0.8rem;
}
.submit{
	width: 40%;
	background-color: #000;
	color: #fff;
	margin: auto;
	text-align: center;
	font-size: 0.875rem;
	line-height: 2rem;
	border-radius: 0.25rem;
	margin: 4.5rem auto;
}



